        default: null
      }
    },
    data() {
      return {
        professional: {
          actuation: "",
          pilot: false,
          experience: 0,
          experienceVants: [],
          ownVants: [],
          actuationRadius: 0
        },
        vants: [
          "batmap",
          "zangão",
          "phantom",
          "maptor"
        ]
      }
    },
    mounted () {

    },
    methods: {
      getFilteredTags(text) {
        this.filteredTags = this.vants.filter((option) => {
          return option
            .toString()
            .toLowerCase()
            .indexOf(text.toLowerCase()) >= 0
        })
      }
    },
  }
</script>

