<template>
  <div class="address-form">
    <b-field
      label="CEP"
      :type="{'is-danger': errors.has('zipCode')}"
      :message="errors.first('zipCode')"
    >
      <b-input
        v-model="address.zipCode"
        v-validate="'required'"
        name="zipCode"
        icon-pack="fas"
        icon="user"
        @input="zipChanged"
        v-mask="'##.###-###'"
      ></b-input>
    </b-field>
    <b-field
      label="Estado"
      :type="{'is-danger': errors.has('state')}"
      :message="errors.first('state')"
    >
      <b-input
        v-model="address.state"
        v-validate="'required'"
        name="state"
        icon-pack="fas"
        icon="user"
        disabled
      ></b-input>
    </b-field>
    <b-field
      label="Cidade"
      :type="{'is-danger': errors.has('city')}"
      :message="errors.first('city')"
    >
      <b-input
        v-model="address.city"
        v-validate="'required'"
        name="city"
        icon-pack="fas"
        icon="user"
        disabled
      ></b-input>
    </b-field>

    <b-field
      label="Endereço"
      :type="{'is-danger': errors.has('address1')}"
      :message="errors.first('address1')"
    >
      <b-input
        v-model="address.address1"
        v-validate="'required'"
        name="address1"
        icon-pack="fas"
        icon="user"
        disabled
      ></b-input>
    </b-field>

    <b-field
      label="Número"
      :type="{'is-danger': errors.has('houseNumber')}"
      :message="errors.first('houseNumber')"
    >
      <b-input
        v-model="address.houseNumber"
        v-validate="'required'"
        name="houseNumber"
        icon-pack="fas"
        icon="user"
      ></b-input>
    </b-field>

    <b-field
      label="Complemento"
      :type="{'is-danger': errors.has('address2')}"
      :message="errors.first('address2')"
    >
      <b-input v-model="address.address2" name="address2" icon-pack="fas" icon="user"></b-input>
    </b-field>

  </div>
</template>

<script>
import cep from 'cep-promise'
export default {
  name: "address-form",
  props: {
    user: {
      type: Object,
      default: null
    },
  },
  data() {
    return {
      address: {
        address1: "",
        address2: "",
        houseNumber: "",
        zipCode: "",
        city: "",
        state: "",
        country: "brazil",
      }
    }
  },
  mounted() {
    if (this.user) {
      this.address = this.$clone(this.user.address) 
    }
  },
  methods: {
    zipChanged(){
      if (this.address.zipCode.length >= 7){
        cep(this.address.zipCode).then((r)=>{
          this.address.state = r.state
          this.address.city = r.city
          this.address.address1 = r.street
        })
        .catch(()=>{})
      }
    },
    submit() {
      return new Promise(async (resolve, reject) => {
        const results = Promise.all([
          this.$validator.validate('zipCode'),
          this.$validator.validate('state'),
          this.$validator.validate('city'),
          this.$validator.validate('address1'),
          this.$validator.validate('houseNumber')
        ]);

        const areValid = (await results).every(isValid => isValid);

        if (areValid) {
          this.address.zipCode = this.address.zipCode.replace(/\D/g,'')
          let editedUser = { 
            id: this.$store.getters.getUser.id,
            address: this.address
          }
          this.$store.dispatch('editLoggedUser', editedUser).then((user)=>{
            resolve(user)
          })
        }
      })
    }
  },
};
</script>

